import { v4 as uuidv4 } from 'uuid';

import { DeckValidationFunction, SerializedFunction } from "../model/types";

type DeckValidationFunctionSupplier = () => DeckValidationFunction;
type SerializedDeckValidationFunctionSupplier = SerializedFunction<DeckValidationFunctionSupplier>;

// function that is stored and passed in various UI component
interface CustomFunction {
  _key: string; // key must be unique
  fname: string;
  description: string;
  code: string; // user input code
  create: (wrapper?: (v: string) => string) => SerializedDeckValidationFunctionSupplier; // build the function
}

class CustomFunctionImpl implements CustomFunction {
  _key: string;
  fname: string;
  description: string;
  code: string;
  // transformFn?: (inputValue: string) => string;
  constructor(fname: string,
    description: string,
    code: string,
    _key?: string) {
    this.fname = fname;
    this.description = description;
    this.code = code;
    this._key = (_key === undefined) ? uuidv4() : _key;
  }

  create(wrapper?: (v: string) => string): SerializedDeckValidationFunctionSupplier {
    const wrap = wrapper === undefined ? noWrapper : wrapper;
    try {
      const fn = new SerializedFunction(Function(`'use strict'; return (${wrap(this.code)})`) as DeckValidationFunctionSupplier);
      return fn;
    } catch (e) {
      throw e;
    }
  }

  static createDefault(): CustomFunction {
    const fname = "myFunction";
    const description = "this function will validate all decks";
    const code = "(() => {\n  /**\n   * Please declare any constant or helper functions below.\n   */\n  // const COMMANDER_NAME = \"Dr. Liang\";\n \n  /**\n   * Validate if a deck can be chosen.\n   * @param deck the deck to validate\n   * @return {boolean} whether this deck can be chosen or not\n   */\n  function validate(deck) {\n    // change the code here\n    return true;\n  }\n\n  return validate;\n})()\n";
    // const code = "/**\n * Please declare any constant or helper functions below.\n * We do not recommended declaring them inside validate function\n */\n// const lookupTable = { Pitbull: true, Titan: false };\n\n/**\n * Validate if a deck can be chosen.\n * @param deck the deck to validate\n * @return {boolean} whether this deck can be chosen or not\n */\nfunction validate(deck) {\n  // change the code here\n  return true;\n}\n";
    return new CustomFunctionImpl(fname, description, code);
  }

  static duplicate(cf: CustomFunction): CustomFunction {
    return new CustomFunctionImpl(cf.fname, cf.description, cf.code);
  }
}

function noWrapper(userInput: string): string {
  return userInput;
}

function defaultWrapper(userInput: string): string {
  // TODO: inject some library functions for clients to use
  return `(() => {\n  'use strict';\n${userInput}\n\n  if (typeof validate === 'undefined' || !(validate instanceof Function)) {\n    throw Error(\"validate is not defined\");\n}\n  return validate;\n})()`;
}

export type { DeckValidationFunctionSupplier, SerializedDeckValidationFunctionSupplier, CustomFunction };
export { CustomFunctionImpl };
