import React, { Component } from 'react';

import { Deck } from '../model/types';

import { Container, Row, Col } from 'react-bootstrap';

// Display 1 deck
interface DeckDisplayProps {
  deck: Deck;
  deckId: string;
  imgs: Map<string, string>;
}

// render a single row of deck
class DeckDisplay extends Component<DeckDisplayProps, {}> {
  render() {
    const IMGS = this.props.imgs;
    const commander = this.props.deck.commander;
    const unitsDisplay = this.props.deck.units.map((u) => {
      return <Col key={u.name} className={"mx-0 px-0"}>
        {/* <img src={IMGS.get(u.name) as string} alt={u.name} height={IMG_HEIGHT} width={IMG_WIDTH}/> */}
        <img src={IMGS.get(u.name) as string} alt={u.name} className={"img-fluid"}/>
        {/* <p>{u.name}</p> */}
      </Col>
    });
    const commanderDisplay = <Col key={commander.name} className={"mx-2 px-0"}>
      <img src={IMGS.get(commander.name) as string} alt={commander.name} className={"img-fluid"}/>
    </Col>
    const emptyCol = (key: string | number) => <Col key={key}></Col>
    const imgs = [emptyCol(1), commanderDisplay, ...unitsDisplay, emptyCol(2)];
    return <Container>
      <Row>
        {/* TODO: display ID? */}
        {imgs}
      </Row>
    </Container>
  }
}

export default DeckDisplay;
