import { Unit, Faction, Building } from "./types";

type Rarity = "Common" | "Rare" | "Epic";

type UnitType = "Infantry" | "Vehicle" | "Air";

type Speed = null | "Slowest" |"Slow" | "Average" | "Fast" | "Faster" | "Fastest";
// const speed = ["Slowest", "Slow", "Average", "Fast", "Faster", "Fastest"]; // to compare speed
const speed = { "Slowest": 1, "Slow": 2, "Average": 3, "Fast": 4, "Faster": 5, "Fastest": 6 }; // to compare speed

const SPEED_ORDER = (a: Speed, b: Speed) => {
  if (a !== null && b !== null) {
    return speed[a] - speed[b];
  } else if (a === null && b !== null) {
    return -1;
  } else if (a !== null && b === null) {
    return 1;
  } else {
    return 0;
  }
}

const requiredFields = ['name', 'squadSize', 'unitType', 'cost', 'building', 'speed', 'faction', 'rarity', 'antiInfantry', 'antiVehicle', 'antiAir'];

class UnitImpl implements Unit {
  readonly name: string;
  readonly faction: Faction;
  readonly building: Building;
  readonly cost: number;
  readonly unitType: UnitType;
  readonly rarity: Rarity;
  readonly speed: Speed;
  readonly squadSize: number;
  readonly antiInfantry: number;
  readonly antiVehicle: number;
  readonly antiAir: number;
  [key: string]: unknown; // other fields

  constructor(unitData: any) {
    this.name = unitData.name;
    this.faction = unitData.faction;
    this.building = unitData.building;
    this.cost = unitData.cost;
    this.unitType = unitData.unitType;
    this.rarity = unitData.rarity;
    this.speed = unitData.speed;
    this.squadSize = unitData.squadSize;
    this.antiInfantry = unitData.antiInfantry;
    this.antiVehicle = unitData.antiVehicle;
    this.antiAir = unitData.antiAir;

    // copy other fields
    for (let [k, v] of Object.entries(unitData)) {
      if (!requiredFields.includes(k)) {
        this[k] = v;
      }
    }
  }

  // unit predicates
  isInfantry(): boolean {
    return this.unitType === "Infantry";
  }

  isVehicle(): boolean {
    return this.unitType === "Vehicle";
  }

  isAir(): boolean {
    return this.unitType === "Air";
  }

  isTech(): boolean {
    return this.building === "Tech";
  }

  canAttackInfantry(): boolean {
    return this.antiInfantry >= 0;
  }

  canAttackVehicle(): boolean {
    return this.antiVehicle >= 0;
  }

  canAttackAir(): boolean {
    return this.antiAir >= 0;
  }

  strongAgainstInfantry(): boolean {
    return this.antiInfantry >= 1;
  }

  strongAgainstVehicle(): boolean {
    return this.antiVehicle >= 1;
  }

  strongAgainstAir(): boolean {
    return this.antiAir >= 1;
  }

  isSquad(): boolean {
    return this.squadSize > 1;
  }
}

export { UnitImpl };
