import React, { Component } from 'react';

import { Container, Row, Col, Button, Form } from 'react-bootstrap';
import { Deck } from '../model/types';
import { DefaultDeckGenerator } from '../runtime/generator';
import DeckDisplayList from './DeckDisplayList';

interface DeckViewerProps {
  currentDeck: string,
  generator: DefaultDeckGenerator,
  imgs: Map<string, string>,
}

interface DeckViewerState {
  deckId: string;
  deck: Deck | undefined;
  error: boolean;
}

// this app allow user to query decks by their id
class DeckViewer extends Component<DeckViewerProps, DeckViewerState> {
  constructor(props: any) {
    super(props);
    this.state = {
      deckId: props.currentDeck,
      deck: undefined,
      error: false,
    }
  }

  render2() {
    return <Container>
      <Row>
        <Col>
          <p className='text-center'>Enter the deck id to query</p>
        </Col>
      </Row>
      <Form>
        <Row>
          <Form.Group className="mb-3" as={Col} md="10" controlId="formNumDeck">
            <Form.Control type="text" placeholder="Deck ID" min={1} step={1} value={this.state.deckId} onChange={(e) => this.onTextChange(e.target.value) }/>
            {this.state.error? <Form.Text className="text-danger">
              Invalid Deck ID
            </Form.Text> : null}
          </Form.Group>

          <Col md="2">
            <Button variant="primary" type="submit" onClick={(e) => {e.preventDefault(); this.queryDeck()}}>Submit</Button>
          </Col>
        </Row>
      </Form>
      {this.renderDisplay()}
    </Container>
  }

  render() {
    return this.render2();
  }

  renderDisplay() {
    if (this.state.deck !== undefined) {
      return <Row>
        <DeckDisplayList decks={[this.state.deck]} imgs={this.props.imgs} generator={this.props.generator}/>
      </Row> 
    } else {
      <Row></Row>
    }
  }

  onTextChange = (value: string) => {
    this.setState({ deckId: value });
  }

  queryDeck = () => {
    try {
      // this will throw error if deck id provided by user is invalid
      const deck = this.props.generator.getDeck(this.state.deckId);
      this.setState({ deck: deck });
      this.setState({ error: false });
    } catch (err) {
      this.setState({ error: true });
    }
  }
}

export default DeckViewer;