import React, { Component, useState } from 'react';

import { Deck } from '../model/types';
import { DefaultDeckGenerator } from '../runtime/init';

import DeckDisplay from './DeckDisplay';

import { Container, Row, Col, ButtonGroup, Button, Collapse, Card, ListGroup } from 'react-bootstrap';

interface DeckDisplayListProps {
  generator: DefaultDeckGenerator;
  decks: Deck[];
  imgs: Map<string, string>;
}

// render a single row of deck
class DeckDisplayList extends Component<DeckDisplayListProps, {}> {
  render() {
    const IMGS = this.props.imgs;
    const DEFAULT_GENERATOR = this.props.generator;
    const displays = this.props.decks.map((d) => {
      const deckId = DEFAULT_GENERATOR.getDeckId(d);
      return <Row key={deckId}>
        {/* <DeckDisplay deckId={deckId} imgs={IMGS} deck={d}/> */}
        <CollapsableTab text={deckId} element={<DeckDisplay deckId={deckId} imgs={IMGS} deck={d}/>}/>
      </Row>
    });
    return (
      <Container>
        <ListGroup>
          {displays}
        </ListGroup>
      </Container>
    );
  }
}

interface CollapsableTabProps {
  text: string;
  element: JSX.Element;
}

function CollapsableTab(props: CollapsableTabProps) {
  const [open, setOpen] = useState(false);

  return (
    <ListGroup.Item action onClick={() => setOpen(!open)} className={"text-center"}>
      {props.text}
      <Collapse in={open}>
        <div className={"my-2"}>
          {props.element}
        </div>
      </Collapse>
    </ListGroup.Item>
  );
}

export default DeckDisplayList;
