import React, { Component, useState } from 'react';
import { Container, Row, Col, Form, Button, Modal } from 'react-bootstrap';

import AceJSEditorWrapper from './AceJSEditorWrapper';

interface EditorPanelProps {
  cfKey: string;
  initialFname: string;
  initialDescription: string;
  initialCode: string;
  onSubmit: (cfKey: string, fname: string, description: string, code: string) => void;
}

interface EditorPanelState {
  fname: string;
  description: string;
  code: string;
  changed: boolean;
  error: string;
}

// this class is the editor panel for the code editor
class EditorPanel extends Component<EditorPanelProps, EditorPanelState> {
  constructor(props: any) {
    super(props);
    this.state = {
      fname: props.initialFname,
      description: props.initialDescription,
      code: props.initialCode,
      changed: false,
      error: "",
    };
  }

  // componentDidUpdate(prevProps: EditorPanelProps, prevState: EditorPanelState) {
  //   if (prevProps.cfKey !== this.props.cfKey) {
  //     if (this.state.changed && window.confirm("Changes not saved, do you want to save?")) {
  //       // catch the case where user has made edit but have not been saved
  //       this.saveCustom(prevProps);
  //     } else {
  //       // if it has been saved, we can load new data safely
  //       // TODO: is it safe to do?
  //       this.setState({ fname: this.props.initialFname, description: this.props.initialDescription, code: this.props.initialCode, changed: false });
  //     }
  //   }
  // }

  componentWillUnmount() {
    // console.log("EditorPanel will be unmounted");
    if (this.state.changed) {
      // alert("unsaved change will be lost!!!");
      if (window.confirm("Changes not saved, do you want to save?")) {
        this.save();
      }
    }
  }

  testRender() {
    let saveBtn, discardBtn;
    if (this.state.changed) {
      saveBtn = <Button variant="primary" type="button" onClick={this.save}>Save</Button>
      discardBtn = <Button variant="danger" type="button" onClick={this.discard}>Discard</Button>
    } else {
      saveBtn = <Button variant="primary" type="button" onClick={this.save} disabled>Saved</Button>
      discardBtn = <Button variant="danger" type="button" onClick={this.discard} disabled>Discard</Button>
    }
    return <Container>
      <Form>
        <Row>
          <Form.Group className="mb-3" as={Col} md="6" controlId="formName">
            <Form.Label>Name</Form.Label>
            <Form.Control type="text" placeholder="name of the function" value={this.state.fname} onChange={(e) => this.fnameOnChange(e.target.value) }/>
            {/* <Form.Text className="text-muted">
              We'll never share your email with anyone else.
            </Form.Text> */}
          </Form.Group>

          <Form.Group className="mb-3" as={Col} md="6" controlId="formDescription">
            <Form.Label>Description</Form.Label>
            <Form.Control type="text" placeholder="decsription of the function (what kind of deck does it validate?)" value={this.state.description} onChange={(e) => this.descriptionOnChange(e.target.value) }/>
          </Form.Group>
        </Row>
      
        <Row>
          <Form.Group className="mb-3" controlId="formPredicate">
            <Form.Label>Predicate</Form.Label>
            <AceJSEditorWrapper value={this.state.code} onChange={this.codeOnChange}/>
          </Form.Group>
        </Row>
        <Row>
          <Col md="6" className={"d-grid"}>
            {saveBtn}
          </Col>
          <Col md="6" className={"d-grid"}>
            {discardBtn}
          </Col>
        </Row>
        {this.state.error ? <Row className='mt-3'>
          <Col>
            <p className='text-danger'>{`Save failed, there are errors in the code: ${this.state.error}`}</p>
          </Col>
        </Row> : null}
      </Form>
    </Container>
  }

  codeOnChange = (value: string) => {
    this.setState({ code: value });
    this.setState({ changed: true });
  }

  fnameOnChange = (value: string) => {
    this.setState({ fname: value });
    this.setState({ changed: true });
  }

  descriptionOnChange = (value: string) => {
    this.setState({ description: value });
    this.setState({ changed: true });
  }

  // saveCustom(thisprops: EditorPanelProps) {
  //   try {
  //     thisprops.onSubmit(this.state.fname, this.state.description, this.state.code);
  //     this.setState({ changed: false, error: "" });
  //   } catch (e) {
  //     this.setState({ error: `${e}` });
  //   }
  // }

  save = () => {
    // this.saveCustom(this.props);
    try {
      this.props.onSubmit(this.props.cfKey, this.state.fname, this.state.description, this.state.code);
      this.setState({ changed: false, error: "" });
    } catch (e) {
      this.setState({ error: `${e}` });
    }
  }

  discard = () => {
    // warn user about discard
    this.setState({ fname: this.props.initialFname, description: this.props.initialDescription, code: this.props.initialCode });
    this.setState({ changed: false });
  }

  render() {
    return this.testRender();
  }
}

function MsgBox() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <Container>
      <Button variant="primary" onClick={handleShow}>
        Launch static backdrop modal
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton={false}>
          <Modal.Title>Unsaved Change</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Do you want to save?
        </Modal.Body>
        <Modal.Footer>
          <Button variant="Danger" onClick={handleClose}>
            Discard
          </Button>
          <Button variant="primary">Save</Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}

export default EditorPanel;
