import React, { Component } from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';

import { Deck } from './model/types';
import { init, DefaultDeckGenerator } from './runtime/init';
import { CustomFunction, CustomFunctionImpl } from './runtime/helper';

import AppSelector from './ui/AppSelector';
import Generator from './ui/Generator';
import CodeEditor from './ui/CodeEditor';
import DeckViewer from './ui/DeckViewer';

import examples from './examples/example';
import { loadExamples } from './examples/example';

import 'bootstrap/dist/css/bootstrap.min.css';

const dataPath = process.env.PUBLIC_URL + "data/units.json";
const imgsPath = process.env.PUBLIC_URL + "data/imgs.json";

const examplesPath = process.env.PUBLIC_URL + "examples/examples.json";

const docLink = "https://github.com/rivalsfan/deck-generator-doc";

type AppSubtype = "Generator" | "CodeEditor" | "DataEditor" | "DeckViewer" | "Help";

interface AppState {
  data: any[], // unit database
  deckGenerator: DefaultDeckGenerator | undefined, // DefaultDeckGenerator
  imgs: Map<string, string> | undefined, // image database
  appSelected: AppSubtype,
  decks: Deck[],
  validateFns: CustomFunction[],
}

class App extends Component<{}, AppState> {
  private navLinks: any;

  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      deckGenerator: undefined,
      imgs: undefined,
      appSelected: "Generator",
      decks: [],
      // validateFns: [...examples], // add a default function for testing
      validateFns: []
    }
  }

  componentDidMount() {
    this.fetchData();
    this.fetchImgs();
    loadExamples(examplesPath, this.updateCfs);
    this.navLinks = [
      <Nav.Link key={"Generator"} eventKey={"Generator"} onClick={() => {
        this.setState({ appSelected: "Generator" });
      }}>Generator</Nav.Link>,
      <Nav.Link key={"GenerCodeEditorator"} eventKey={"CodeEditor"} onClick={() => {
        this.setState({ appSelected: "CodeEditor" });
      }}>Code Editor</Nav.Link>,
      <Nav.Link key={"DataEditor"} eventKey={"DataEditor"} onClick={() => {
        this.setState({ appSelected: "DataEditor" });
      }}>Data Editor</Nav.Link>,
      <Nav.Link key={"DeckViewer"} eventKey={"DeckViewer"} onClick={() => {
        this.setState({ appSelected: "DeckViewer" });
      }}>Deck Viewer</Nav.Link>,
      <Nav.Link key={"Help"} eventKey={"Help"} onClick={() => {
        this.setState({ appSelected: "Help" });
      }}>Help</Nav.Link>,
    ];
  }

  fetchData = async () => {
    const res = await fetch(dataPath);
    const data = await res.json();
    this.setState({
      data: data,
      deckGenerator: init(data)
    });
  }

  fetchImgs = async () => {
    const res = await fetch(imgsPath);
    const data = await res.json();
    this.setState({
      imgs: new Map(data.map((e: any) => [e.key, e.value]))
    });
  }

  updateCfs = (newCfs: CustomFunction[]) => {
    this.setState({ validateFns: newCfs });
  }

  renderSubapp(DEFAULT_GENERATOR: DefaultDeckGenerator, IMGS: Map<string, string>): JSX.Element {
    if (this.state.appSelected === "Generator") {
      return <Generator generator={DEFAULT_GENERATOR} imgs={IMGS} validateFns={this.state.validateFns}/>
    }
    if (this.state.appSelected === "CodeEditor") {
      return <CodeEditor onUpdate={this.updateCfs} cfs={this.state.validateFns}/>
    }
    if (this.state.appSelected === "DeckViewer") {
      return <DeckViewer currentDeck={"gdi-0"} generator={DEFAULT_GENERATOR} imgs={IMGS}/>
    }
    if (this.state.appSelected === "Help") {
      return (
        <p>
          Checkout the documentation <a href={docLink} target="_blank" rel="noopener noreferrer">here</a>
        </p>
      );
    }
    return <p>Coming soon</p>
  }

  render() {
    if (typeof this.state.deckGenerator === 'undefined') {
      return <div className="App">
        <p>loading data</p>
      </div>
    } else if (typeof this.state.imgs === 'undefined') {
      return <div className="App">
        <p>loading imgs</p>
      </div>
    } else {
      const DEFAULT_GENERATOR = this.state.deckGenerator;
      // const testDeck = DEFAULT_GENERATOR.GDI_DECKS.get(48937);
      const IMGS = this.state.imgs;
      return <div className="App">
        <AppSelector title={"Rivals Deck Generator"} navLinks={this.navLinks} activeKey={this.state.appSelected}/>
        <Container className={"mt-4"}>
          {this.renderSubapp(DEFAULT_GENERATOR, IMGS)}
        </Container>
      </div>
    }
  }
}

export default App;
