import React, { Component } from 'react';

import { Container, Navbar, Nav } from 'react-bootstrap';

interface AppSelectorProps {
  title: string,
  navLinks: JSX.Element[], // rendering prop, should have type Nav.Link
  activeKey: string,
}

// the navbar that allow user to select Apps
class AppSelector extends Component<AppSelectorProps, {}> {
  render() {
    return <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="#home">{this.props.title}</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto text-center" variant="tabs" activeKey={this.props.activeKey}>
            {this.props.navLinks}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  }
}

export default AppSelector;