import { Unit, Request, Response, ProgressCallback } from "../model/types";
import { DefaultDeckGenerator } from "./generator";
import * as Comlink from "comlink";

function createWorker(): Worker {
  return new Worker(new URL('./worker.ts', import.meta.url));
}

// this will create a local instance (itself) and a remote instance (using comlink)
class LocalDeckGenerator extends DefaultDeckGenerator {
  remoteGenerator: Comlink.Remote<DefaultDeckGenerator> | undefined;
  worker: Worker;
  initRemote: () => void;
  constructor(GDI_UNITS: Unit[], GDI_COMMANDERS: Unit[], NOD_UNITS: Unit[], NOD_COMMANDERS: Unit[]) {
    super(GDI_UNITS, GDI_COMMANDERS, NOD_UNITS, NOD_COMMANDERS);
    this.worker = createWorker();
    this.initRemote = () => {
      // console.log("init remote called");
      const RemoteDeckGenerator = Comlink.wrap<typeof DefaultDeckGenerator>(this.worker);
      new RemoteDeckGenerator(GDI_UNITS, GDI_COMMANDERS, NOD_UNITS, NOD_COMMANDERS).then(g => {
        this.remoteGenerator = g;
        // console.log("remote initialization complete")
      }).catch(e => { console.log(e) });
    }
    this.initRemote();
  }

  generate(requests: Request[], progressCb?: ProgressCallback): Response[] {
    if (this.remoteGenerator !== undefined) {
      console.log("Warning: avoid synchronous call on generate()");
    }
    return super.generate(requests, progressCb);
  }

  // use remote generation when possible
  generateAsync(requests: Request[], responseFn: (response: Response[]) => void, progressCb?: ProgressCallback): void {
    if (this.remoteGenerator !== undefined) {
      if (progressCb !== undefined) {
        this.remoteGenerator.generate2(requests, Comlink.proxy(progressCb)).then(responseFn);
      } else {
        this.remoteGenerator.generate2(requests).then(responseFn);
      }
    } else {
      super.generateAsync(requests, responseFn, progressCb);
    }
  }

  // cancel the current generation
  cancel() {
    if (this.remoteGenerator !== undefined) {
      this.worker.terminate();
      this.worker = createWorker();
      this.initRemote();
    } else {
      // should never reach here
      throw new Error("remote endpoint does not exist");
    }
  }
}

function init(data: any[]): DefaultDeckGenerator {
  const GDI_ALL = data.filter(d => d.faction === "GDI");
  const NOD_ALL = data.filter(d => d.faction === "Nod");
  const GDI_UNITS = GDI_ALL.filter(d => d.building !== "Commander");
  const GDI_COMMANDERS = GDI_ALL.filter(d => d.building === "Commander");
  const NOD_UNITS = NOD_ALL.filter(d => d.building !== "Commander");
  const NOD_COMMANDERS = NOD_ALL.filter(d => d.building === "Commander");

  return new LocalDeckGenerator(GDI_UNITS, GDI_COMMANDERS, NOD_UNITS, NOD_COMMANDERS);
}

export { init, DefaultDeckGenerator };
