import React, { Component } from 'react';

import { Response } from '../model/types';
import { DefaultDeckGenerator } from '../runtime/init';

import DeckDisplayList from './DeckDisplayList';

import { Card } from 'react-bootstrap';

interface ResponseDisplayProps {
  generator: DefaultDeckGenerator;
  response: Response;
  imgs: Map<string, string>;
}

// wrapper of DeckDisplay List
class ResponseDisplay extends Component<ResponseDisplayProps, {}> {
  renderNormal() {
    let desc = `There are ${this.props.response.count} deck(s) satisfies the above predicate, below is ${this.props.response.result.length} of them`;
    return (
      <Card>
        <Card.Body>
            <Card.Text>
                {desc}
            </Card.Text>
            <DeckDisplayList generator={this.props.generator} imgs={this.props.imgs} decks={this.props.response.result}/>
        </Card.Body>
      </Card>
    );
  }

  renderException() {
    let desc = `${this.props.response.error}`;
    return (
      <Card>
        <Card.Body>
            <Card.Text className={"text-danger"}>
                {desc}
            </Card.Text>
        </Card.Body>
      </Card>
    );
  }

  render() {
    if (this.props.response.error === undefined) {
      return this.renderNormal();
    } else {
      return this.renderException();
    }
  }
}

export default ResponseDisplay;
