import { CustomFunction, CustomFunctionImpl } from "../runtime/helper";

// import list of example functions
import ex00AllDecks from "./ex00-all-decks";
import ex01ContainsCommander from "./ex01-contains-commander";
import ex02ContainsUnits from "./ex02-contains-units";
import ex03222Deck from "./ex03-2-2-2-deck";
import ex04CheapDeck from "./ex04-cheap-deck";
import ex05MutantChuggy from "./ex05-mutant-chuggy";
import ex06WellRoundedDecks from "./ex06-well-rounded-decks";
import ex07TotalCost from "./ex07-total-cost";
import ex08VehiclesMoreExpensiveThanInfantry from "./ex08-vehicles-more-expensive-than-infantry";
import ex09WellRoundedDecksAdvanced from "./ex09-well-rounded-decks-advanced";

function unwrap(code: string) {
  return `(${code})()`;
}

// load the examples
async function loadExamples(metadataUrl: string, cb: (cfs: CustomFunction[]) => void) {
  // first download metadata
  const res = await fetch(metadataUrl);
  const data: any[] = await res.json();

  // then download actual data (concurrently)
  const ps = data.map(async d => {
    const exampleUrl = d["url"];
    const r = await fetch(exampleUrl);
    const code = await r.text();
    return new CustomFunctionImpl(d.fname, d.description, code, d._key);
  });
  cb(await Promise.all(ps));
}

// this will not work in production as the example code will be mimified
const examples: CustomFunction[] = [
  ex00AllDecks,
  ex01ContainsCommander,
  ex02ContainsUnits,
  ex03222Deck,
  ex04CheapDeck,
  ex05MutantChuggy,
  ex06WellRoundedDecks,
  ex07TotalCost,
  ex08VehiclesMoreExpensiveThanInfantry,
  ex09WellRoundedDecksAdvanced,
].map(d => new CustomFunctionImpl(d.fname, d.description, unwrap(d.code), d._key));

export default examples;
export { loadExamples };
