import React, { Component } from 'react';
import { Container, Row, Col, Form, Button, InputGroup } from 'react-bootstrap';

import GenerationControl from './GenerationControl';
import ResponseDisplay from './ResponseDisplay';

import { Response } from '../model/types';
import { RequestImpl } from '../model/model';

import { Faction } from '../model/types';
import { DefaultDeckGenerator } from '../runtime/init';
import { CustomFunction } from '../runtime/helper';

interface GeneratorProps {
  validateFns: CustomFunction[],
  generator: DefaultDeckGenerator,
  imgs: Map<string, string>,
  // Generator saved state
}

interface GeneratorState {
  results: Response[] | undefined;
  progress: number;
}

class Generator extends Component<GeneratorProps, GeneratorState> {
  constructor(props: any) {
    super(props);
    this.state = {
      results: undefined,
      progress: -1,
    }
  }

  // Create a request (using this state) for generation
  generate = (faction: "random" | Faction, numDeck: number, cf: CustomFunction) => {
    const fn = cf.create();
    this.props.generator.generateAsync([new RequestImpl(faction, cf.fname, fn, numDeck)], res => { this.setState({ results: res, progress: -1, }) }, this.updateGenerationProgress);
    // animation generating
    this.setState({ progress: 0 });
  }

  updateGenerationProgress = /*async*/ (progress: number) => {
    // console.log(`progress called: ${progress}`);
    this.setState(prevState => {
      if (prevState.progress >= 0) {
        return { progress: progress };
      } else {
        return { progress: -1 };
      }
    });
  }

  cancel = () => {
    // alert("Cannot be cancelled, please refresh the page :(");
    this.props.generator.cancel();
    this.setState({ progress: -1 });
  }

  render() {
    if (this.state.results !== undefined) {
      const displayResults = this.state.results.map((res, idx) => {
        return <ResponseDisplay key={idx} generator={this.props.generator} imgs={this.props.imgs} response={res} />
      });
      return <Container>
        <GenerationControl onSubmit={this.generate} validateFns={this.props.validateFns} generationProgress={this.state.progress} onCancel={this.cancel}/>
        <Container className={"my-3"}>
          {displayResults}
        </Container>
      </Container>
    } else {
      return <Container>
        <GenerationControl onSubmit={this.generate} validateFns={this.props.validateFns} generationProgress={this.state.progress} onCancel={this.cancel}/>
      </Container>
    }
  }
}

export default Generator;
