import React, { Component } from 'react';

import { Container, Row, Col, ListGroup } from 'react-bootstrap';

import { CustomFunction } from '../runtime/helper';

interface FunctionListProps {
  cfs: CustomFunction[]; // list of functions
  activeIdx: number | undefined; // which one is shown selected
  onSelect: (index: number) => void; // when something is selected
}

// display a list of functions (by their name)
class FunctionList extends Component<FunctionListProps, {}> {
  renderItem = (cf: CustomFunction, idx: number) => {
    if (this.props.activeIdx === idx) {
      return <ListGroup.Item key={idx} action onClick={() => this.props.onSelect(idx)} active>{cf.fname}</ListGroup.Item>
    } else {
      return <ListGroup.Item key={idx} action onClick={() => this.props.onSelect(idx)}>{cf.fname}</ListGroup.Item>
    }
  }

  render() {
    const data = this.props.cfs.map(this.renderItem);
    if (data.length === 0) {
      return <Container>
        <p className='text-center'> Please create at least 1 function</p>
      </Container>
    } else {
      return <Container>
        <ListGroup>
          {data}
        </ListGroup>
      </Container>
    }
  }
}

export default FunctionList;
