import React, { Component } from 'react';

import 'ace-builds';
// import 'ace-builds/webpack-resolver';

import AceEditor from 'react-ace';

// the import of ACE is a mess
// see here for detail: https://github.com/securingsincity/react-ace/issues/725
import "ace-builds/src-noconflict/mode-javascript";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/ext-language_tools";
// import "ace-builds/src-noconflict/snippets/javascript";

import ace from 'ace-builds/src-noconflict/ace';

// import jsWorker from '!!file-loader!ace-builds/src-noconflict/worker-javascript';
// ace.config.setModuleUrl("ace/mode/json_worker", jsWorker);
/* eslint import/no-webpack-loader-syntax: off */
// import xmlWorkerUrl from "file-loader!ace-builds/src-noconflict/worker-xml";
ace.config.set("basePath", "https://cdn.jsdelivr.net/npm/ace-builds@1.4.3/src-noconflict/");
ace.config.setModuleUrl('ace/mode/javascript_worker', "https://cdn.jsdelivr.net/npm/ace-builds@1.4.3/src-noconflict/worker-javascript.js");

interface AceJSEditorWrapperProps {
  value: string;
  onChange: (value: string) => void;
}

class AceJSEditorWrapper extends Component<AceJSEditorWrapperProps, {}> {
  // constructor(props: any) {
  //   super(props);
  //   // this.state = {
  //   //   value: initialValue,
  //   // }
  // }

  onTextChange(newValue: string) {
    // console.log(newValue);
    this.setState({ value: newValue });
  }

  render() {
    return <div>
      <AceEditor mode="javascript"
      theme="github"
      onChange={this.props.onChange}
      name="UNIQUE_ID_OF_DIV"
      value={this.props.value}
      editorProps={{
        $blockScrolling: true,
      }}
      setOptions={{
        enableBasicAutocompletion: true,
        enableLiveAutocompletion: true,
        // enableSnippets: true
        useSoftTabs: true,
        navigateWithinSoftTabs: true,
      }}
      tabSize={2}
      width="auto"
      height="50vh"/**//>
    </div>
  }
}

export default AceJSEditorWrapper;