import { Faction, Unit, Deck } from './types';

// this class implements the Deck interface
// it also serve as a standard library for clients (who writes validateFn) to use
class DeckImpl implements Deck {
  readonly faction: Faction;
  readonly commander: Unit;
  readonly units: Unit[];

  constructor(faction: Faction, commander: Unit, units: Unit[]) {
    this.faction = faction;
    this.commander = commander;
    this.units = units;
  }

  // general deck building functions

  hasCommander(name: string): boolean {
    return this.commander.name === name;
  }

  containsUnitsByName(names: string[]): boolean {
    return names.every(mcu => this.units.map(u => u.name).includes(mcu));
  }

  containsUnitByName(name: string): boolean {
    return this.containsUnitsByName([name]);
  }

  // unit predicates

  allUnitsThat(fn: (u: Unit) => boolean): boolean {
    return this.units.every(fn);
  }

  someUnitsThat(fn: (u: Unit) => boolean): boolean {
    return this.units.some(fn);
  }

  // n between 0 to 6 inclusive
  nUnitsThat(n: number, fn: (u: Unit) => boolean): boolean {
    return this.units.filter(fn).length === n;
  }

  // range are 2 numbers, from 0 to 6 inclusive
  rangeUnitsThat(range: [number, number], fn: (u: Unit) => boolean): boolean {
    const res = this.units.filter(fn).length;
    return res >= range[0] && res <= range[1];
  }

  atLeastNUnitsThat(n: number, fn: (u: Unit) => boolean): boolean {
    return this.rangeUnitsThat([n, 6], fn);
  }

  atMostNUnitsThat(n: number, fn: (u: Unit) => boolean): boolean {
    return this.rangeUnitsThat([0, n], fn);
  }

  // whole deck predicate helpers

  totalUnitsCost(n: number): number {
    return this.units.reduce((acc, u) => {
      acc += u.cost;
      return acc;
    }, 0);
  }
}

export { DeckImpl };