import React, { Component } from 'react';
import { Container, Row, Col, Form, Button, ProgressBar } from 'react-bootstrap';

import { Faction } from '../model/types';
import { CustomFunction } from '../runtime/helper';

interface GenerationControlProps {
  // return generation result to parent (App)
  // onResult: (result: Response[]) => void;
  onSubmit: (faction: Faction | "random" , numDeck: number, predicate: CustomFunction) => void;
  onCancel: () => void;
  validateFns: CustomFunction[],
  generationProgress: number; // -1 means not generating, otherwise 0-100
}

interface GenerationControlState {
  faction: Faction | "random";
  numDecks: number;
  cfSelected: CustomFunction | undefined;
}

const factionOptions = ["random", "GDI", "Nod"];

// this class contains the control (user input) for the Generator
// the user input is passed to parent via callback when user submit their request
class GenerationControl extends Component<GenerationControlProps, GenerationControlState> {
  readonly factionOptionsHTML;
  constructor(props: any) {
    super(props);
    this.state = {
      faction: "random",
      numDecks: 1,
      cfSelected: props.validateFns.length > 0 ? props.validateFns[0] : undefined,
    };
    this.factionOptionsHTML = factionOptions.map(fo => <option key={fo} value={fo}>{fo}</option>);
  }

  testRender() {
    // TODO: what will be a good identifier for CustomFunction? name may or may not be unique (or should it be unique?)
    const cfsOptions = this.props.validateFns.map((cf, idx) => <option key={idx} value={idx}>{cf.fname}</option>);
    let btnGenerate;
    if (this.props.generationProgress >= 0) {
      btnGenerate = <Row>
        <Col md="10" className={"d-grid"}>
          <ProgressBar animated now={this.props.generationProgress} label={`Generating... ${this.props.generationProgress}%`} style={{ height: "35px" }}/>
        </Col>
        <Col md="2" className={"d-grid"}>
          <Button variant={"danger"} onClick={this.props.onCancel}>Cancel</Button>
        </Col>
      </Row>
      // <Button variant="primary" type="button" disabled onClick={this.onSubmit}>Generating</Button>
    } else {
      btnGenerate = <Row>
        <Col md="12" className={"d-grid"}>
          <Button variant="primary" type="button" onClick={this.onSubmit}>Generate</Button>
        </Col>
      </Row>
    }
    let desc = `Generate ${this.state.numDecks} deck(s) of ${this.state.faction} faction that `;
    if (this.state.cfSelected !== undefined) {
      desc = desc + `satisfies '${this.state.cfSelected.fname}'`;
    } else {
      desc = desc + `...`;
    }
    return <Container>
      <Form>
        <Row>
          <Form.Group className="mb-3" as={Col} md="6" controlId="formNumDeck">
            <Form.Label>Maximum Decks</Form.Label>
            <Form.Control type="number" placeholder="Maximum number of decks" min={1} step={1} value={this.state.numDecks} onChange={(e) => this.numDeckOnChange(e.target.value) }/>
            {/* <Form.Text className="text-muted">
              Some text
            </Form.Text> */}
          </Form.Group>

          <Form.Group className="mb-3" as={Col} md="6" controlId="formFaction">
            <Form.Label>Faction</Form.Label>
            <Form.Select aria-label="Default select faction" value={this.state.faction} onChange={(e) => { this.factionOnChange(e.target.value); }}>
              {this.factionOptionsHTML}
            </Form.Select>
          </Form.Group>
        </Row>
      
        <Row>
          <Form.Group className="mb-3" controlId="formPredicate">
            <Form.Label>Predicate</Form.Label>
            <Form.Select aria-label="Default select predicete" onChange={(e) => { this.fnOnChange(e.target.value); }}>
              {cfsOptions}
            </Form.Select>
          </Form.Group>
        </Row>
        <Row>
          <Col md="12">
            <p>{desc}</p>
          </Col>
        </Row>
        {btnGenerate}
      </Form>
    </Container>
  }

  numDeckOnChange = (value: string) => {
    const num = parseInt(value);
    if (!isNaN(num) && num >= 1) {
      this.setState({ numDecks: num });
    }
  }

  factionOnChange = (value: string) => {
    this.setState({ faction: value as (Faction | "random") });
  }

  fnOnChange = (value: string) => {
    this.setState({ cfSelected: this.props.validateFns[parseInt(value)] });
  }

  onSubmit = () => {
    if (this.state.cfSelected !== undefined) {
      this.props.onSubmit(this.state.faction, this.state.numDecks, this.state.cfSelected);
    } else {
      alert("please select at least 1 predicate")
    }
  }

  render() {
    return this.testRender();
  }
}

export default GenerationControl;
