  //   // }
  //   if (prevProps.cfs.length !== this.props.cfs.length) {
  //     this.setState({ selected: -1 });
  //   }
  // }

  // onTextChange = (newValue: string) => {
  //   // console.log(newValue);
  //   this.setState({ value: newValue });
  // }

  // testRender() {
  //   return <div>
  //     <p>Note: All code you typed here will only be run in your browser, nothing is sent to server unless otherwise mentioned.</p>
  //     <p id="warning"><b>WARNING: Never use code you don't trust !!!</b></p>
  //     <AceJSEditorWrapper value={this.state.value} onChange={this.onTextChange}/>  
  //     <button onClick={() => {
  //       // for debugging
  //       console.log(JSON.stringify(this.state.value));
  //       // console.log(Function(`'use strict'; return (${wrap(this.state.value)})`)());
  //       // console.log(eval(wrap(this.state.value)));
  //       try {
  //         const res = new SerializedFunction(Function(`'use strict'; return (${wrap(this.state.value)})`) as DeckValidationFunctionSupplier);
  //         this.props.onSubmit(res);
  //       } catch (err) {
  //         // TODO: tell client something is wrong
  //         console.log(err);
  //       }
  //     }}>test</button>
  //   </div>
